.Pagination-Wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.Pagination-Block {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 7px;
  margin: 5px;
  cursor: pointer;
  color: white;
  background-color: #c700cb;
  &:hover {
    background-color: #e55be7;
  }
}