
.Search-Input {
  box-sizing: border-box;
  margin: 0px;
  font-variant: tabular-nums;
  list-style: none;
  position: relative;
  display: inline-block;
  width: 40%;
  min-width: 0;
  padding: 4px 11px;
  color: #595c97;
  font-size: 14px;
  line-height: 2;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #e4e9f0;
  border-radius: 2px;
  transition: all 0.3s;
  &:hover,
  &:focus,
  &:active {
    outline: none;
    border: 1px solid;
    border-color: #595c97;
  }
}

.Search-Input-Title {
  font-size: 18px;
  font-family: Roboto;
  text-align: start;
  margin-bottom: 4px;
  font-weight: bold;
  width: 90%;
}

.Products {
  display: flex;
  flex-direction: column;
  padding: 50px;
  @media (max-width: 768px) {
    padding: 20px;
  }
  .Products-Header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: #e4e4e4 1px solid;
    margin: 0px 50px;
    margin-bottom: 10px;
    @media (max-width: 768px) {
      margin: 0px;
    }
    .Products-Header-Title {
      font-family: Roboto;
      font-size: 44px;
      font-weight: 800;
      padding-bottom: 10px;
    }
    .Products-Header-Button {
      min-width: 40px;
      .Products-Header-Button-Icon {
        font-size: 30px;
      }
      .Products-Header-Button-Sort {
        border-width: 1px;
        border-color: red;
        background-color: blue;
      }
    }
  }
  .Products-Filter {
    font-size: 16px;
    font-family: Roboto;
    font-weight: 600;
    padding: 7px 15px;
    margin-right: 15px;
    cursor: pointer;
    border-radius: 15px;
    border: 2px solid #c700cb;
    text-decoration: none;
    color: #c700cb;
    &:active {
      color: #c700cb;
    }
    &:hover {
      background-color: #c700cb;
      color: white;
    }
  }
  .Products-Table {
    border-collapse: collapse;
    margin: 25px 0;
    font-size: 0.9em;
    font-family: sans-serif;
    width: 100%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    .Products-Table-Image {
      height: 80px;
    }
    .Products-Table-Edit {
      font-size: 26px;
      color: #c700cb;
    }
    .Products-Table-Delete {
      font-size: 26px;
      color: #ff0000;
    }
    .Products-Table-Title {
      font-size: 16px;
      font-family: Roboto;
      font-weight: 500;
    }
    thead tr {
      background-color: #c700cb;
      color: #ffffff;
      text-align: left;
    }
    tbody tr {
      border-bottom: 1px solid #dddddd;
      padding: 12px 15px;
    }

    tbody tr:last-of-type {
      border-bottom: 2px solid #c700cb;
    }
    td,
    th {
      padding: 12px 15px;
    }
  }
}
